/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body {
  font-family: "Roboto";
  overflow-x: hidden;
  max-width: 1440px;
  margin: 0 auto;
}

div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

.section {
  display: flex;
  flex-direction: column;
  row-gap: 2.5rem;
}
.section__img {
  display: block;
  position: relative;
  padding-top: 100%;
}
.section__img img {
  position: absolute;
  width: 100% !important;
  height: 100%;
  top: 0;
  left: 0;
}
.section__content {
  display: flex;
  flex-direction: column;
  row-gap: 1.875rem;
}
@media (min-width: 768px) {
  .section__img {
    padding-top: 66.45%;
  }
}
@media (min-width: 1024px) {
  .section {
    flex-direction: row;
    justify-content: space-between;
  }
  .section__img {
    width: 51.54%;
    padding-top: 33.68139%;
  }
  .section__content {
    width: 38.78%;
  }
}

.modal {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(0);
  transition: 200ms ease-in-out;
  border-radius: 10px;
  z-index: 10;
  background-color: white;
  width: 100%;
  max-width: 80%;
}
.modal.active {
  transform: translate(-50%, -50%) scale(1);
}
.modal__body {
  display: flex;
  flex-direction: column;
  row-gap: 1.25rem;
  position: absolute;
  padding: 1.5625rem;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 63.88%;
  background-color: #fff;
}
.modal__close {
  cursor: pointer;
  font-size: 1.25rem;
  position: absolute;
  top: 25px;
  right: 25px;
  transform: translate(50%, -50%);
  border: none;
  background-color: transparent;
}
.modal__overlay {
  position: fixed;
  opacity: 0;
  transition: 200ms ease-in-out;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(18, 18, 18, 0.5);
  pointer-events: none;
}
.modal__overlay.active {
  opacity: 1;
  pointer-events: all;
}

.a-container {
  padding-left: 1.875rem;
  padding-right: 1.875rem;
}
@media (min-width: 768px) {
  .a-container {
    padding-left: 0;
    padding-right: 0;
    max-width: 688px;
    margin: 0 auto;
  }
}
@media (min-width: 1024px) {
  .a-container {
    max-width: 734px;
  }
}
@media (min-width: 1366px) {
  .a-container {
    max-width: 926px;
  }
}
@media (min-width: 1440px) {
  .a-container {
    max-width: 980px;
  }
}

.a-top {
  padding-top: 1.875rem;
}
@media (min-width: 768px) {
  .a-top {
    padding-top: 2.5rem;
  }
}
@media (min-width: 1024px) {
  .a-top {
    padding-top: 3.75rem;
  }
}
@media (min-width: 1440px) {
  .a-top {
    padding-top: 6rem;
  }
}

.a-bottom {
  padding-top: 1.875rem;
}
@media (min-width: 768px) {
  .a-bottom {
    padding-top: 2.5rem;
  }
}
@media (min-width: 1024px) {
  .a-bottom {
    padding-top: 3.75rem;
  }
}
@media (min-width: 1440px) {
  .a-bottom {
    padding-top: 6rem;
  }
}

.a-title {
  font-family: "Roboto";
  font-weight: 700;
  font-style: normal;
  font-size: clamp(2rem, 3vw, 2.625rem);
  line-height: 1.2;
}

.a-description {
  font-family: "Roboto";
  font-weight: 300;
  font-style: normal;
  font-size: clamp(0.75rem, 3vw, 1rem);
  line-height: 1.5;
}

.a-button {
  width: fit-content;
  padding: 1rem 2.5rem;
  display: block;
  background-color: #e4c1ae;
  font-size: 1rem;
  line-height: 1;
  font-weight: 700;
  border-radius: 2rem;
  border: none;
  cursor: pointer;
}

.dark {
  color: #121212;
}

.white {
  color: #fff;
}